import React from 'react';
import { NavLink } from 'react-router-dom';
import styles from './Header.module.scss';

export default function Header() {
  const setActiveLink = ({ isActive }) => isActive ? styles.active : '';

  return (
    <header className={styles.header}>
      <div className={styles.wrapper}>
        <p className={styles.logo}>KVOLIKDUB</p>
        <nav>
          <ul className={styles.pcNav}>
            <li><NavLink className={setActiveLink} to='/'></NavLink></li>
            <li><NavLink className={setActiveLink} to='/soon'></NavLink></li>
            <li><NavLink className={setActiveLink} to='/contacts'> </NavLink></li>
          </ul>
        </nav>
        <NavLink className='primary-button' to='login'></NavLink>
      </div>
    </header>
  )
}
